import { Injectable } from '@angular/core';
import { Socket } from 'ngx-socket-io';

@Injectable({
  providedIn: 'root'
})

export class WebsocketService extends Socket{

  constructor() { 
    super({
      url: 'https://up-articulos.herokuapp.com'
    })
    
  }
  conectar() {
    this.ioSocket.connect();
  }

  desconectar() {
    this.ioSocket.disconnect();
  }
}
